$DESKTOP: "DESKTOP";
$MEDIUM_DEVICEs: "MEDIUM_DEVICEs";
$TABLET_DEVICEs: "TABLET_DEVICEs";
$WIDE_MOBILE: "WIDE_MOBILE";
$SMALL_DEVICES: "SMALL_DEVICES";

@mixin Responsive($canvas){
    @if ($canvas) == $DESKTOP {      
        @media only screen and (min-width: 1200px) {
            @content;
        }
    }
    @else if($canvas == $MEDIUM_DEVICEs) {
        @media only screen and (min-width: 992px) and (max-width:1199px) {
            @content;
        }
    }
    @else if($canvas == $TABLET_DEVICEs) {
        @media only screen and (min-width: 768px) and (max-width:991px) {
            @content;
        }
    }
    @else if($canvas == $WIDE_MOBILE) {
        @media only screen  and (max-width:767px) {
            @content;
        }
    }
    @else if($canvas == $SMALL_DEVICES) {
        @media only screen and (max-width:575px) {
            @content;
        }
    }
}